<template>
  <div>

    <div>
      <v-parallax
        dark
        src="../assets/img/parallax2.jpg"
        height="250"
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col class="text-center" cols="12">
            <h1 class="display-1 font-weight-black mb-4">Lo mejor en gomas y suspensión</h1>
            <h4 class="font-italic font-weight-regular mb-2">Conoce nuestras variedad de productos</h4>
            <v-btn download="Catalogo.pdf" href="./download/Catalogo.pdf" color="success" large rounded>Catalogo</v-btn>
          </v-col>
        </v-row>
      </v-parallax>
    </div>
    
    <div class="container">
      <v-row>
        <v-col cols="1" md="3">
          <template>
              <v-navigation-drawer
                permanent
                color="grey lighten-2"
                class="hidden-sm-and-down"

              >
                <v-list>

                  <!-- <v-list-item class="px-2">
                    <v-list-item-avatar>
                      <v-img src="https://randomuser.me/api/portraits/women/85.jpg"></v-img>
                    </v-list-item-avatar>
                  </v-list-item> -->

                  <v-list-item link>
                    <v-list-item-content>
                      <v-list-item-title class="title">Productos</v-list-item-title>
                      <v-list-item-subtitle>Categorias</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>

                <v-divider></v-divider>

                <v-list-group
                  prepend-icon="mdi-tag"
                  color="info"
                  value="true"
                  nav
                  v-for="(item, index) in categorias" :key="index"
                >

                  <template v-slot:activator>
                    <v-list-item-title class="body-1 font-weight-medium">
                      {{item[0]}}
                    </v-list-item-title>
                  </template>

                      <v-list-item
                        v-for="(i, index) in item[1]" :key="index"
                        link
                      >
                        <v-list-item-icon>
                          <v-icon>mdi-label-outline</v-icon>
                        </v-list-item-icon>
                        <v-list-item-title class="caption">{{i}}</v-list-item-title>
                      </v-list-item>

                </v-list-group>

              </v-navigation-drawer>

             
          </template>
        </v-col>

        <v-col cols="12" md="9">

          <v-toolbar
            color="#DEDEDE"
          >
            <v-text-field
              hide-details
              single-line
              prepend-inner-icon="mdi-magnify"
              color="indigo"
              placeholder="¿Qué estás buscando?"
              class="mx-2"
            ></v-text-field>

            <v-btn dark color="indigo" >
              Buscar
            </v-btn>
          </v-toolbar>

          <div class=" mt-5 display-1 font-weight-black text-center">NUEVOS PRODUCTOS</div>

          <v-slide-group class="mt-3" multiple show-arrows>
            <v-slide-item v-for="(item, index) in items" :key="index">
              <v-card 
                class="ma-4"
                width="200"
              >
                <v-img
                  class="white--text align-end"
                  height="200px"
                  :src="require(`../assets/img/${item}`)" 
                  contain
                >
                  <div><v-chip class="ml-3 mb-2" x-small color="error">NUEVO</v-chip></div>
                </v-img>
                <v-list-item three-line>
                <v-list-item-content>
                  <v-list-item-title class="headline mb-1">Producto</v-list-item-title>
                  <v-list-item-subtitle class="overline">{{item}}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
              </v-card>
            </v-slide-item>
          </v-slide-group>

          <v-divider class="my-5"></v-divider>

          <div class=" mt-12 display-1 font-weight-black text-center">CATEGORIA DE PRODCUTOS</div>
          <div class=" mb-4 overline text-center">DESCRIPCION DE CATEGORIA DE PRODCUTOS</div>

          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="4"
              v-for="(item, index) in items" :key="index"
            >
              <v-card
                class="mx-auto"
                max-width="400"
              >
                <v-img
                  class="white--text align-end"
                  height="200px"
                  :src="require(`../assets/img/${item}`)"
                >
                </v-img>

                <v-card-title>Nombre del producto</v-card-title>
                <v-card-subtitle class="pb-0 ">{{item}}</v-card-subtitle>

                <v-card-text class="text--primary">
                  <div>Marca de automovil</div>

                  <div>Descripcion completa del producto.</div>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="success"
                    text
                  >
                    Ver mas...
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>

        </v-col>
      </v-row>
    </div>

  </div>
</template>

<script>
export default {
    name: 'Productos',
    data() {
      return {
        categorias: [
          ['Categoria 1', ['Sub-categoria 1', 'Sub-categoria 2', 'Sub-categoria 3', 'Sub-categoria 4', 'Sub-categoria 5',]],
          ['Categoria 2', ['Sub-categoria 1', 'Sub-categoria 2', 'Sub-categoria 3', 'Sub-categoria 4', 'Sub-categoria 5',]],
          ['Categoria 3', ['Sub-categoria 1', 'Sub-categoria 2', 'Sub-categoria 3', 'Sub-categoria 4', 'Sub-categoria 5',]],
          ['Categoria 4', ['Sub-categoria 1', 'Sub-categoria 2', 'Sub-categoria 3', 'Sub-categoria 4', 'Sub-categoria 5',]]
        ],
        items: ['DF-001.jpg', 'DF-002.jpg', 'DF-009-2.jpg', 'DF-009-3.jpg', 'DF-10-12.jpg', 'DF-10-13.jpg', 'DF-0010.jpg', 'DF-12-200.jpg', 'DF-0012.jpg', 'DF-012.jpg', 'DF-015.jpg', 'DF-046.jpg', 'DF-071.jpg']
      }
    },
}
</script>

